.main-nav {
  position: relative;
  width: auto;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  /*@media (max-width: @mobile-width-only) {
    .main-nav--closed .site-list__item {
      display: none;
    }
  }*/
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    width: 100%;
    background-color: #ffffff;
  }
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  width: 36px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
/*.main-nav--opened .main-container__hero-wrapper {
  padding-top: 201px;
}*/
.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}
.main-nav__toggle {
  position: absolute;
  top: -60px;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 51px;
  margin-right: 8px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
